.postsComponent{
    margin-right: 15vw;
    padding: 40px;
    font-family: monospace;
    color: white;
    font-size: 15px;
}
.postsalignment{
    display: flex;
    justify-content: center;
    flex-wrap: wrap; 
    gap: 25px;
}
.productDescript{
    background-color: #be8c86;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    width: 200px;
    padding: 20px;
}
.productsbtn{
    background-color: #0aa8b9;
    color: white;
    font-family: monospace;
    font-size: 15px;
}
.welcomeosts{
    display: flex;
    justify-content: space-between;
    padding: 0px 25px;
    height: 5vh;
    align-items: center;
    background-color: #0aa8b9;
    margin-bottom: 15px;
}