.postsAC{
    display: flex;
    flex-direction: column;
    align-items: first baseline;
}
.regLogContainer{
    width: 40vw;
}
.postsABtn{
    background-color: rgb(252, 199, 179);
    padding: 5px;
    font-weight: bold;
    margin: 10px;
}
.postCard{
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    align-items: first baseline;
    margin-bottom: 3px;
}
.spanTitleContent{
    color: black;
    text-decoration: underline 1px black solid;
}
.postsbtns{
    display: flex;
    flex-direction: column;
    width: 7vw;
}
.posttwo{
    width: 750px;
    margin-top: 30px;
    margin-left: 30px;
    padding: 30px;
    
    background-color: rgb(143, 136, 136);
}
.shoowposts{
    display: flex;
    flex-direction: column;
}
.postsTwoCardFav{
    display: flex;
    align-items: center;
    gap: 10px;
}
.postsTwoCard{
    border: 1px solid black;
    margin-bottom: 10px;
    padding: 15px;
    background-color: lightgray;

}
.iconFavTwo{
    height: 20px;
    width: 20px;
}
.fullStar{

    height: 20px;
    width: 20px;
    color: rgb(71, 37, 37);
}
.iconFavTwo:hover{
    cursor: pointer;
}