.regLogComponentContainer{
    display: flex;
    justify-content: start;
}
.regLogContainer{
    padding: 20px;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    background-color: rgb(167, 211, 167);
    margin: 50px;
    color: darkgreen;
}
.regLogForm{
    display: flex;
    flex-direction: column;
    align-items: first baseline;
    gap: 12px;
}
.titlecontent{
    display: flex;
    flex-direction: row;
    align-items: first baseline;
    gap: 10px;
}
.regLogBtn{
    background-color: lightseagreen;
    padding: 10px;
    font-size: 15px;
    color: greenyellow;
}
.linkLogReg{
    color: black;
}