.loader {
    margin: auto;
    border: 20px solid #EAF0F6;
    border-radius: 50%;
    border-top: 20px solid green;
    width: 100px;
    height: 100px;
    animation: spinner 4s linear infinite;
    margin-top: 40px;
 }
 
 @keyframes spinner {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
 }