.sidebar {
  margin: 0;
  padding: 0; 
  position: fixed;
  right: 0;
  top: 0; 
  height: 100vh; 
  width: 200px;   
  background-color: #79C9D2;
}
.organizeSidebar {
  display: flex;
  flex-direction: column;
  padding: 15px;
  font-size: 18px;
}
.sidebarLinks{
  text-decoration: none;
  color: black;
  margin: 5px 0;
}
.sidebarLinks:hover {
  color: #048bc0; 
}
.menuName{
  color: #048bc0;
  background-color: #7fdee9;
  text-align: center;
}